<template>
  <div class="register">
    <navgation-bar @onLeftClick="onBackClick" :pageName="'注册'"></navgation-bar>
    <!-- 内容区 -->
    <div class="register-content">
      <!-- 用户名 -->
      <div class="input-container">
        <input type="text" v-model="username" placeholder="用户名" />
      </div>
      <!-- 密码 -->
      <div class="input-container">
        <input type="password" v-model="password" placeholder="密码" />
      </div>
      <!-- 确认密码 -->
      <div class="input-container">
        <input type="password" v-model="confirmPassword" placeholder="确认密码" />
      </div>
      <div class="btn" @click="onRegisterClick">注册</div>
    </div>
  </div>
</template>
<script>
import NavgationBar from '../components/currency/NavgationBar.vue'
export default {
  components: {
    NavgationBar
  },
  data() {
    return {
      username: '',
      password: '',
      confirmPassword: ''
    }
  },
  methods: {
    onBackClick() {
      this.$router.go(-1)
    },
    onRegisterClick() {
      if (this.username.length === 0 || this.password.length === 0) {
        alert('请输入用户名密码')
        return
      }
      if (this.password !== this.confirmPassword) {
        alert('两次密码不一致')
        return ''
      }
    }
  }
}
</script>
<style lang="less" scoped>
.register {
  margin: 76/20rem 0;
}
</style>
